import React, { useEffect, useState } from 'react';
import { Container, Row, Col, Card, ButtonGroup, Button, Pagination, Form } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import { FaTh, FaList } from 'react-icons/fa'; // Import icons
import supabase from '../../../../supabaseClient';
import './BlogHome.css';

function BlogHome() {
  const [blogs, setBlogs] = useState([]);
  const [currentPage, setCurrentPage] = useState(1);
  const [view, setView] = useState('grid'); // Default view is grid
  const blogsPerPage = 6;
  const [searchTerm, setSearchTerm] = useState('');

  useEffect(() => {
    fetchBlogs();
  }, [searchTerm]);

  const fetchBlogs = async () => {
    let query = supabase.from('blogs').select('*');

    if (searchTerm) {
      query = query.or(`
        title.ilike.%${searchTerm}%,
        author.ilike.%${searchTerm}%,
        tags.ilike.%${searchTerm}%
      `);
    }

    let { data: blogs, error } = await query;
    if (error) {
      console.log('Error fetching blogs:', error);
    } else {
      console.log('Fetched blogs:', blogs); // Log the fetched blogs
      setBlogs(blogs);
    }
  };

  const handlePageClick = (pageNumber) => {
    setCurrentPage(pageNumber);
  };

  const startIndex = (currentPage - 1) * blogsPerPage;
  const endIndex = startIndex + blogsPerPage;
  const currentBlogs = blogs.slice(startIndex, endIndex);

  const totalPages = Math.ceil(blogs.length / blogsPerPage);

  return (
    <Container className="blog-home" >
      <Row className="mb-3">
        <Col md={8}>
          <Form.Control
            type="text"
            placeholder="Search by title, author, tags"
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
          />
        </Col>
        <Col md={4} className="text-end">
          <ButtonGroup>
            <Button variant={view === 'grid' ? 'primary' : 'outline-primary'} onClick={() => setView('grid')} size="sm">
              <FaTh /> 
            </Button>
            <Button variant={view === 'list' ? 'primary' : 'outline-primary'} onClick={() => setView('list')} size="sm">
              <FaList /> 
            </Button>
          </ButtonGroup>
        </Col>
      </Row>
      <Row>
        {currentBlogs.map(blog => (
          view === 'grid' ? (
            <Col md={4} key={blog.id} className="mb-4" >
              <Card className="blog-card">
                <Card.Img variant="top" src={blog.thumbnail} className="blog-card-img" />
                <Card.Body>
                  <Card.Title className="blog-card-title">{blog.title}</Card.Title>
                  <Card.Subtitle className="mb-2 text-muted">{blog.author}</Card.Subtitle>
                  <Card.Text className="blog-card-tags">{blog.tags.split(',').map(tag => `#${tag} `)}</Card.Text>
                  <Link to={`/blog/${blog.id}`} className="btn btn-primary">Read More</Link>
                </Card.Body>
              </Card>
            </Col>
          ) : (
            <Col md={12} key={blog.id} className="mb-4" >
              <Card className="blog-card list-view">
                <Row>
                  <Col md={4}>
                    <Card.Img src={blog.thumbnail} className="blog-card-img" />
                  </Col>
                  <Col md={8}>
                    <Card.Body>
                      <Card.Title className="blog-card-title">{blog.title}</Card.Title>
                      <Card.Subtitle className="mb-2 text-muted">{blog.author}</Card.Subtitle>
                      <Card.Text className="blog-card-tags">{blog.tags.split(',').map(tag => `#${tag} `)}</Card.Text>
                      <Card.Text className="blog-card-content">{blog.content.substring(0, 100)}...</Card.Text>
                      <Link to={`/blog/${blog.id}`} className="btn btn-primary">Read More</Link>
                    </Card.Body>
                  </Col>
                </Row>
              </Card>
            </Col>
          )
        ))}
      </Row>
      <Pagination className="justify-content-center">
        <Pagination.First onClick={() => handlePageClick(1)} disabled={currentPage === 1} />
        <Pagination.Prev onClick={() => handlePageClick(currentPage - 1)} disabled={currentPage === 1} />
        {[...Array(totalPages)].map((_, index) => (
          <Pagination.Item
            key={index + 1}
            active={index + 1 === currentPage}
            onClick={() => handlePageClick(index + 1)}
          >
            {index + 1}
          </Pagination.Item>
        ))}
        <Pagination.Next onClick={() => handlePageClick(currentPage + 1)} disabled={currentPage === totalPages} />
        <Pagination.Last onClick={() => handlePageClick(totalPages)} disabled={currentPage === totalPages} />
      </Pagination>
    </Container>
  );
}

export default BlogHome;
