import React, { useEffect, useState } from 'react';
import { Container, Row, Col, Button, Table } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import supabase from '../../../supabaseClient';
// import './BlogManagement.css'; // Create a CSS file for custom styles

function BlogManagement() {
  const [blogs, setBlogs] = useState([]);

  useEffect(() => {
    fetchBlogs();
  }, []);

  const fetchBlogs = async () => {
    let { data: blogs, error } = await supabase
      .from('blogs')
      .select('*');
    if (error) console.log('Error fetching blogs:', error);
    else setBlogs(blogs);
  };

  const deleteBlog = async (id) => {
    let { error } = await supabase
      .from('blogs')
      .delete()
      .eq('id', id);
    if (error) console.log('Error deleting blog:', error);
    else fetchBlogs();
  };

  return (
    <Container className="blog-management" data-aos="fade-up">
      <Row>
        <Col md={12} className="mb-4">
          <Link to="/blog/new" className="btn btn-primary">Add New Blog</Link>
        </Col>
      </Row>
      <Row>
        <Col md={12}>
          <Table striped bordered hover>
            <thead>
              <tr>
                <th>Title</th>
                <th>Author</th>
                <th>Actions</th>
              </tr>
            </thead>
            <tbody>
              {blogs.map(blog => (
                <tr key={blog.id}>
                  <td>{blog.title}</td>
                  <td>{blog.author}</td>
                  <td>
                    <Link to={`/blog/edit/${blog.id}`} className="btn btn-warning btn-sm">Edit</Link>{' '}
                    <Button variant="danger" size="sm" onClick={() => deleteBlog(blog.id)}>Delete</Button>
                  </td>
                </tr>
              ))}
            </tbody>
          </Table>
        </Col>
      </Row>
    </Container>
  );
}

export default BlogManagement;
