import React from "react";
import { Navbar, Nav, Container, Row, Col, Button } from "react-bootstrap";
import { Link } from "react-router-dom"; // Import Link
import "./HeroNavBar.css";

function HeroNavBar() {
  return (
    <div className="hero-navbar" data-aos="fade-up">
      <Navbar expand="lg">
        <Container>
          <Navbar.Brand href="/" className="text-white" data-aos="fade-down">
            Reformed Missional Church
          </Navbar.Brand>
          <Navbar.Toggle aria-controls="basic-navbar-nav" />
          <Navbar.Collapse id="basic-navbar-nav">
            <Nav className="me-auto">
              <Nav.Link href="#home" className="text-white">Home</Nav.Link>
              <Nav.Link href="#sermons" className="text-white">Sermons</Nav.Link>
              <Nav.Link href="#ministries" className="text-white">Ministries</Nav.Link>
              <Nav.Link href="#events" className="text-white">Events</Nav.Link>
              <Nav.Link href="#contact" className="text-white">Contact</Nav.Link>
              <Nav.Item>
                <Link to="/blog" className="nav-link text-white">Blog</Link>
              </Nav.Item>
            </Nav>
          </Navbar.Collapse>
        </Container>
      </Navbar>
      <div className="hero-content" data-aos="fade-up">
        <Container>
          <Row className="align-items-center">
            <Col md={6} data-aos="fade-right">
              <h3>Welcome to</h3>
              <h1> Reformed Missional Church!</h1>
              <p>
                An oasis of truth that will transform you! <br />
                We pray and hope God would use the resources in our site to help
                you grow in the knowledge of Gods word and glorify His name.
              </p>
              <Button variant="primary" href="#services" data-aos="fade-up">
                Learn More
              </Button>
            </Col>
            <Col md={6} data-aos="fade-left">
              <div className="video-container">
                <img src="sq-5.jpg" alt="Video " data-aos="zoom-in" />
                <Button variant="light" className="play-button" data-aos="zoom-in">
                  
                </Button>
              </div>
            </Col>
          </Row>
        </Container>
      </div>
    </div>
  );
}

export default HeroNavBar;
