import React, { useState, useCallback } from 'react';
// import { useParams } from "react-router-dom";
import { Card } from 'react-bootstrap';
import { FaFacebook, FaTwitter, FaLinkedin, FaWhatsapp } from 'react-icons/fa';
// import { Helmet } from 'react-helmet';
// import supabase from "../../../../supabaseClient";
import './CustomShareButton.css';


function CustomShareButton({ url, title, description, image, author }) {
  const shareBlog = (title, url, author) => {
    if (navigator.share) {
      navigator.share({
        title:title,
        url: url,
        author: author,
      }).then(() => {
        console.log("Thanks for sharing!");
      }).catch((error) => {
        console.log("Sharing failed", error);
      });
    } else {
      const shareUrl = `https://www.facebook.com/sharer/sharer.php?u=${encodeURIComponent(url)}`;
      const twitterUrl = `https://twitter.com/intent/tweet?url=${encodeURIComponent(url)}&text=${encodeURIComponent(title)}`;
      const linkedinUrl = `https://www.linkedin.com/sharing/share-offsite/?url=${encodeURIComponent(url)}`;
      const whatsappUrl = `https://api.whatsapp.com/send?text=${encodeURIComponent(title)}%20${encodeURIComponent(url)}`;

      window.open(shareUrl, "_blank");
      window.open(twitterUrl, "_blank");
      window.open(linkedinUrl, "_blank");
      window.open(whatsappUrl, "_blank");
    }
  };
  // const [metaTags, setMetaTags] = useState(null);
  // const { id } = useParams();

  // const fetchBlog = useCallback(async () => {
  //   let { data: metaTags, error: metaError } = await supabase
  //     .from("meta_tags")
  //     .select("*")
  //     .eq("blog_id", id)
  //     .single();
  //   if (metaError) {
  //     console.error("Error fetching meta tags:", metaError);
  //   } else {
  //     console.log("Meta tags data:", metaTags);
  //     setMetaTags(metaTags);
  //   }
  // }, [id]);


  return (
    <>
      {/* <Helmet> 
        <title>{metaTags ? metaTags.title : 'Loading...'}</title> 
      <meta name="description" content={metaTags.description || ""} /> 
      <meta property="og:title" content={metaTags.title} /> 
      <meta property="og:description" content={metaTags.description || ""} /> 
      <meta property="og:url" content={window.location.href} /> 
      <meta property="article:author" content={metaTags.author} /> 
      </Helmet> */}
      <Card className="share-card">
        <Card.Img variant="top" src={image} alt={title} />
        <Card.Body>
          <Card.Title>{title}</Card.Title>
          <Card.Text>{description}</Card.Text>
          <Card.Text>{author}</Card.Text>
          <div className="share-icons">
            <a href={`https://www.facebook.com/sharer/sharer.php?u=${encodeURIComponent(url)}`} target="_blank" rel="noopener noreferrer" className="m-1">
              <FaFacebook size={32} />
            </a>
            <a href={`https://twitter.com/intent/tweet?url=${encodeURIComponent(url)}&text=${encodeURIComponent(title)}`} target="_blank" rel="noopener noreferrer" className="m-1">
              <FaTwitter size={32} />
            </a>
            <a href={`https://www.linkedin.com/sharing/share-offsite/?url=${encodeURIComponent(url)}`} target="_blank" rel="noopener noreferrer" className="m-1">
              <FaLinkedin size={32} />
            </a>
            <a href={`https://api.whatsapp.com/send?text=${encodeURIComponent(title)}%20${encodeURIComponent(url)}`} target="_blank" rel="noopener noreferrer" className="m-1">
              <FaWhatsapp size={32} />
            </a>
          </div>
        </Card.Body>
      </Card>
    </>
  );
}

export default CustomShareButton;
