import React from 'react';
import { Container, Row, Col, Card } from 'react-bootstrap';
import './RecentSermons.css'; // Assuming we create a CSS file for custom styles

function RecentSermons() {
  const sermons = [
    {
      date: 'March 20, 2022',
      pastor: 'Pastor Campbell',
      title: 'LIVE THE MESSAGE',
      description: 'Far far away, behind the word mountains, far from the countries Vokalia and Consonantia.',
      videoUrl: 'https://www.youtube.com/embed/5BCQpzN574g?si=TL_T5hAFCjmtqsdX" ', // Replace with actual video ID
    },
    {
      date: 'March 15, 2022',
      pastor: 'Pastor Campbell',
      title: 'DOERS NOT HEARERS',
      description: 'Far far away, behind the word mountains, far from the countries Vokalia and Consonantia.',
      videoUrl: 'https://www.youtube.com/embed/TJ865y2ynTo?si=gcRfWbSWoGPSN4nQ', // Replace with actual video ID
    },
    {
      date: 'March 10, 2022',
      pastor: 'Pastor Campbell',
      title: 'BY FAITH NOT BY SIGHT',
      description: 'Far far away, behind the word mountains, far from the countries Vokalia and Consonantia.',
      videoUrl: 'https://www.youtube.com/embed/XShUprLihEs?si=jQjPNEF62oJbymFQ', // Replace with actual video ID
    },
    {
        date: 'March 10, 2022',
        pastor: 'Pastor Campbell',
        title: 'BY FAITH NOT BY SIGHT',
        description: 'Far far away, behind the word mountains, far from the countries Vokalia and Consonantia.',
        videoUrl: 'https://www.youtube.com/embed/SN9kHTKGf84?si=RX70gefTLzu4o9Cn', // Replace with actual video ID
      },
  ];

  return (
    <div className="recent-sermons">
      <Container>
        <Row>
          {sermons.map((sermon, index) => (
            <Col md={3} key={index}>
              <Card className="mb-4 sermon-card">
                <div className="embed-responsive embed-responsive-16by9">
                  <iframe className="embed-responsive-item" src={sermon.videoUrl} allowFullScreen title={sermon.title}></iframe>
                </div>
                <Card.Body>
                  <Card.Subtitle className="mb-2 text-muted">{sermon.date} - {sermon.pastor}</Card.Subtitle>
                  <Card.Title>{sermon.title}</Card.Title>
                  <Card.Text>{sermon.description}</Card.Text>
                </Card.Body>
              </Card>
            </Col>
          ))}
        </Row>
      </Container>
    </div>
  );
}

export default RecentSermons;
