import React from 'react';
import { Container, Row, Col } from 'react-bootstrap';
import './Footer.css'; // Import the CSS

function Footer() {
  return (
    <footer className="footer" >
      <Container>
        <Row>
          <Col md={3} data-aos="fade-right">
            <h5>Contact Us</h5>
            <p>
              Reformed Missional Church<br />
              123 Church Street, City<br />
              Phone: (123) 456-7890<br />
              Email: info@reformedchurch.org
            </p>
          </Col>
          <Col md={3} data-aos="fade-up">
            <h5>Service Times</h5>
            <p>
              Sunday Morning: 10:00 AM<br />
              Sunday Evening: 6:00 PM<br />
              Wednesday Prayer Meeting: 7:00 PM
            </p>
          </Col>
          <Col md={3} data-aos="fade-left">
            <h5>Quick Links</h5>
            <ul className="quick-links">
              <li><a href="#home">Home</a></li>
              <li><a href="#about">About Us</a></li>
              <li><a href="#sermons">Sermons</a></li>
              <li><a href="#ministries">Ministries</a></li>
              <li><a href="#events">Events</a></li>
              <li><a href="#contact">Contact</a></li>
            </ul>
          </Col>
          <Col md={3} data-aos="fade-right">
            <h5>Follow Us</h5>
            <ul className="social-media-list">
              <li><a href="https://facebook.com" target="_blank" rel="noopener noreferrer"><i className="fab fa-facebook-f"></i></a></li>
              <li><a href="https://twitter.com" target="_blank" rel="noopener noreferrer"><i className="fab fa-twitter"></i></a></li>
              <li><a href="https://instagram.com" target="_blank" rel="noopener noreferrer"><i className="fab fa-instagram"></i></a></li>
              <li><a href="https://youtube.com" target="_blank" rel="noopener noreferrer"><i className="fab fa-youtube"></i></a></li>
            </ul>
          </Col>
        </Row>
        <Row className="mt-3">
          <Col md={12} className="text-center" data-aos="fade-up">
            <p>&copy; {new Date().getFullYear()} Reformed Missional Church. All rights reserved.</p>
          </Col>
        </Row>
      </Container>
    </footer>
  );
}

export default Footer;
