// export default BlogPost;

import React, { useEffect, useState, useCallback } from "react";
import { Container, Row, Col, OverlayTrigger, Tooltip } from "react-bootstrap";
import { useParams, useNavigate } from "react-router-dom";
import { FaArrowLeft, FaArrowUp, FaShareAlt } from "react-icons/fa";
// import { Helmet } from "react-helmet";
import CustomShareButton from "../CustomShareButton/CustomShareButton";
import supabase from "../../../../supabaseClient";
import "./BlogPost.css";

function BlogPost() {
  const { id } = useParams();
  const navigate = useNavigate();
  const [blog, setBlog] = useState(null);
  const [metaTags, setMetaTags] = useState(null);
  const [tooltipText] = useState("Copy link");

  const fetchBlog = useCallback(async () => {
    let { data: blog, error } = await supabase
      .from("blogs")
      .select("*")
      .eq("id", id)
      .single();
    if (error) {
      console.error("Error fetching blog:", error);
    } else {
      console.log("Blog data:", blog);
      setBlog(blog);
    }
  }, [id]);

  useEffect(() => {
    fetchBlog();
  }, [fetchBlog]);

  const handleBackClick = () => {
    navigate("/blog");
  };

  const handleScrollTop = () => {
    window.scrollTo({ top: 0, behavior: "smooth" });
  };

  const formatContent = (content) => {
    return content
      .split("\n")
      .map((paragraph, index) => <p key={index}>{paragraph}</p>);
  };

  const handleCopyLink = () => {
    const title = blog.title;
    const url = `${window.location.origin}/#/blog/${id}`;
    shareBlog(title, url);
  };
  function shareBlog(title, url) {
    if (navigator.share) {
      navigator
        .share({
          title: title,
          url: url,
        })
        .then(() => {
          console.log("Thanks for sharing!");
        })
        .catch((error) => {
          console.log("Sharing failed", error);
        });
    } else {
      const shareUrl = `https://www.facebook.com/sharer/sharer.php?u=${encodeURIComponent(
        url
      )}`;
      const twitterUrl = `https://twitter.com/intent/tweet?url=${encodeURIComponent(
        url
      )}&text=${encodeURIComponent(title)}`;

      window.open(shareUrl, "_blank");
      window.open(twitterUrl, "_blank");
    }
  }

  if (!blog) {
    return (
      <div className="loader-container">
        <div className="loader"></div>
      </div>
    );
  }

  return (
    <Container className="blog-post" data-aos="fade-up">
      {/* <Helmet>
        <title>{metaTags.title}</title>
        <meta name="description" content={metaTags.description || ""} />
        <meta property="og:title" content={metaTags.title} />
        <meta property="og:description" content={metaTags.description || ""} />
        <meta property="og:image" content={metaTags.image || ""} />
        <meta property="og:url" content={window.location.href} />
        <meta property="article:author" content={metaTags.author} />
      </Helmet> */}
      <Row>
        <Col md={10} lg={12} sm={10} xs={10} className="mx-auto">
          <div className="icon-buttons">
            <FaArrowLeft className="back-icon" onClick={handleBackClick} />
            <OverlayTrigger
              placement="bottom"
              overlay={<Tooltip id={`tooltip-${id}`}>{tooltipText}</Tooltip>}
            >
              <FaShareAlt className="share-icon" onClick={handleCopyLink} />
            </OverlayTrigger>
          </div>
          <h1 className="blog-title">{blog.title}</h1>
          <p className="text-muted">
            by {blog.author} on {new Date(blog.date).toLocaleDateString()}
          </p>
          <img
            src={blog.thumbnail}
            alt={blog.title}
            className="img-fluid blog-thumbnail mb-4"
          />
          <div className="blog-content">{formatContent(blog.content)}</div>
          <CustomShareButton
            url={window.location.href}
            title={blog.title}
            image={blog.thumbnail}
            description={blog.tags || ""}
            author={blog.author}
          />
          <div className="back-to-top">
            <FaArrowUp className="top-icon" onClick={handleScrollTop} />
          </div>
        </Col>
      </Row>
    </Container>
  );
}

export default BlogPost;
